import React, { useState } from "react";
import "./Mockup.css";
import { Form, Stack, Spinner } from "react-bootstrap";
import CustomButton from "../../components/Button/Button";
import FileUpload from "../../components/FileUploader/FileUploader";
import CustomModal from "../../components/Modal/customModal";
import { downloadTemplate } from "../../utils/downloadTemplate";
import useToast from "../../hooks/useToast";
import * as XLSX from "xlsx";
import config from "./config.json";
import Loader from "../../components/Loader/Loader";
const MassUpload = () => {
  const [modalShow, setModalShow] = useState(false);
  const [operationChoice, setOperationChoice] = useState("");
  const [validationErrors, setValidationErrors] = useState([]);
  const [showErrorLink, setShowErrorLink] = useState(false);
  const { showErrorToast } = useToast();

  // Move operationMap outside functions so it can be shared
  const operationMap = {
    1: "Physical Product",
    2: "Physical Product Structure",
    3: "Document",
    4: "Physical Product-Document",
  };

  const validateHeaders = (headers) => {
    if (!operationChoice) return [];

    const selectedOperation = operationMap[operationChoice];
    if (!selectedOperation) return [];

    // Get mandatory attributes from config
    const mandatoryFields =
      config.MassUpload[selectedOperation]?.mandatoryAttributes;
    if (!mandatoryFields) {
      console.error(
        `No mandatory attributes found for operation: ${selectedOperation}`
      );
      return [];
    }

    console.log("Selected Operation:", selectedOperation);
    console.log("Headers in File:", headers);
    console.log("Mandatory Fields:", mandatoryFields);

    // Case-insensitive comparison of headers
    const missingHeaders = mandatoryFields.filter(
      (attr) =>
        !headers.some((header) => header.toLowerCase() === attr.toLowerCase())
    );

    return missingHeaders;
  };

  const handleFileUpload = (files) => {
    if (!files.length) return;

    const reader = new FileReader();
    reader.onload = (e) => {
      try {
        const data = new Uint8Array(e.target.result);
        const workbook = XLSX.read(data, { type: "array" });
        const sheet = workbook.Sheets[workbook.SheetNames[0]];

        // Read headers with options
        const jsonData = XLSX.utils.sheet_to_json(sheet, {
          header: 1,
          raw: false,
          defval: "",
        });

        if (jsonData.length > 0) {
          // Clean and normalize headers
          const headers = jsonData[0].map((header) =>
            header ? header.toString().trim() : ""
          );

          const errors = validateHeaders(headers);

          if (errors.length) {
            setValidationErrors(errors);
            setShowErrorLink(true);
            showErrorToast(
              "File contains errors. Click 'Content Error' for details."
            );
          } else {
            // Validate data in rows
            const dataRows = jsonData.slice(1);
            const selectedOperation = operationMap[operationChoice];
            const mandatoryFields =
              config.MassUpload[selectedOperation]?.mandatoryAttributes;
            const dataErrors = [];

            dataRows.forEach((row, rowIndex) => {
              mandatoryFields.forEach((field) => {
                const colIndex = headers.findIndex(
                  (h) => h.toLowerCase() === field.toLowerCase()
                );
                if (colIndex === -1 || !row[colIndex]) {
                  dataErrors.push(`Row ${rowIndex + 2}: ${field} is empty`);
                }
              });
            });

            if (dataErrors.length) {
              setValidationErrors(dataErrors);
              setShowErrorLink(true);
              showErrorToast("Some required fields are empty");
            } else {
              setValidationErrors([]);
              setShowErrorLink(false);
              showErrorToast("File validated successfully!");
            }
          }
        }
      } catch (error) {
        console.error("Error processing file:", error);
        showErrorToast("Error processing file");
      }
    };

    reader.readAsArrayBuffer(files[0]);
  };

  const handleDownloadTemplate = () => {
    downloadTemplate(showErrorToast)(operationChoice);
  };

  return (
    <>
      <Stack className="mt-3" gap={4}>
        <Stack direction="horizontal">
          <Form.Select
            aria-label="Choose Operations"
            style={{
              width: "30%",
              border: "1px solid #007bff",
              outline: "none",
              borderRadius: "8px",
            }}
            onChange={(e) => setOperationChoice(e.target.value)}
          >
            <option value="">Choose Operations</option>
            <option value="1">Physical Product/Raw Material</option>
            <option value="2">Physical Product Structure</option>
            <option value="3">Document</option>
            <option value="4">Physical Product-Document</option>
          </Form.Select>

          <CustomButton
            variant="link ms-auto btn-lg"
            onClick={handleDownloadTemplate}
            text="Download Template"
          />
        </Stack>

        <FileUpload
          fileTypes={["XLSX"]}
          multiple={false}
          onUpload={handleFileUpload}
        />

        <Stack direction="horizontal">
          {showErrorLink && (
            <CustomButton
              variant="danger"
              text="Content Error"
              onClick={() => setModalShow(true)}
            />
          )}
          <Form.Check type="checkbox" label="Background" className="ms-auto" />
          <CustomButton variant="primary" text="Submit" />
        </Stack>
      </Stack>

      <CustomModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        title="Validation Errors"
        footerButtons={[
          {
            label: "Close",
            variant: "danger",
            onClick: () => setModalShow(false),
          },
        ]}
      >
        <ul>
          {validationErrors.map((error, index) => (
            <li key={index}>{error} is missing from the template</li>
          ))}
        </ul>
      </CustomModal>
    </>
  );
};

export default MassUpload;
